.slider-arrow {
  width: 50px;
  height: 100px;
  position: absolute;
  top: 158px;
  z-index: 1;
  opacity: 0.2;
  -webkit-transition: 300ms;
  transition: 300ms;
  cursor: pointer;
  visibility: hidden;
}
.slider-arrow:hover {
  opacity: 1;
}
.slider-arrow:nth-child(1) {
  left: 30px;
}
.slider-arrow:nth-child(1)::before {
  content: '';
  height: 4px;
  width: 70px;
  position: absolute;
  top: 49px;
  left: 0;
  border-radius: 2px;
  background-color: #fff;
  -webkit-transform-origin: left;
  transform-origin: left;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transition: 300ms;
  transition: 300ms;
}
.slider-arrow:nth-child(1)::after {
  content: '';
  height: 4px;
  width: 72px;
  position: absolute;
  top: 51px;
  left: 0;
  border-radius: 2px;
  background-color: #fff;
  -webkit-transform-origin: left;
  transform-origin: left;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transition: 300ms;
  transition: 300ms;
}
.slider-arrow:nth-child(2) {
  right: 30px;
}
.slider-arrow:nth-child(2)::before {
  content: '';
  height: 4px;
  width: 70px;
  position: absolute;
  top: 49px;
  right: 0;
  border-radius: 2px;
  background-color: #fff;
  -webkit-transform-origin: right;
  transform-origin: right;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transition: 300ms;
  transition: 300ms;
}
.slider-arrow:nth-child(2)::after {
  content: '';
  height: 4px;
  width: 70px;
  position: absolute;
  top: 51px;
  right: 0;
  border-radius: 2px;
  background-color: #fff;
  -webkit-transform-origin: right;
  transform-origin: right;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transition: 300ms;
  transition: 300ms;
}
.slider-arrow:active::before {
  width: 50px;
}
.slider-arrow:active::after {
  width: 50px;
}
